.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../images/background.jpg");
}
.main_logo {
  margin-left: 60px;
  margin-top: 200px;
  padding: 100px;
  width: 150px;
}
.cards {
  height: 150vh;
  display: grid;
  grid-template-columns: 1fr;

  list-style: none;
}
.card {
  height: 180px;
  width: 120px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  border-radius: 10px;
  border: 2px solid greenyellow;
  text-decoration: none;
}
.card_img {
  margin-top: 15px;
  height: 90px;
  border-radius: 20px;
  border: 2px solid greenyellow;
}
.card_name {
  text-align: center;
  color: greenyellow;
  font-size: 12px;
  margin-bottom: 0px;
  font-weight: 900;
}
.card_specie {
  font-size: 10px;
  color: white;
  font-weight: 600;
}

/*Formulario*/

.form {
  margin-top: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: greenyellow;
  align-items: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 30px;
}
.form_label_specie {
  margin-top: 10px;
}

.preview_card {
  color: white;
}
.not_found_text {
  color: white;
}

.link_decoration {
  text-decoration: none;
}

/*Character Details*/

.preview_card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: turquoise;
  height: 50vh;
  width: 70vw;
  margin-bottom: 200px;
  border-radius: 5px;
  border: solid 5px greenyellow;
}

.preview_image {
  margin-left: 30px;
  height: 200px;
  border-radius: 10px;
  border: solid 2px greenyellow;
}
.details_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.preview_name {
  margin-left: 30px;
  color: greenyellow;
  font-weight: 900;
}
.preview_details {
  margin-left: 10px;
  color: darkblue;
}
.link_back {
  text-align: center;
  text-decoration: none;
  color: white;
  margin-left: 180px;
  margin-top: 35px;
  font-weight: 900;
  background-color: darkblue;
  width: 80px;
  height: 25px;
  border-radius: 10px;
}

.form_input_select {
  margin-top: 10px;
  background-color: darkblue;
  border: solid 2px greenyellow;
  border-radius: 10px;
  color: white;
  font-weight: 900;
}

/* tablet */

@media all and (min-width: 768px) {
  .form {
    margin-top: 250px;
  }

  .main_logo {
    width: 400px;
  }
  .cards {
    height: 130vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
  }
}

@media all and (min-width: 1024px) {
  .form {
    margin-top: -60px;
  }
  .main_logo {
    width: 500px;
  }
  .cards {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
  }
}
